<template>
  <div class="wrapper">
    <div class="multiplicate">
      <form class="form-signin">
        <div class="header">
          <i class="fa fa-lock" />
          <h1 class="h3 mb-3 font-weight-normal">HR Strategy</h1>
        </div>
        <div class="alert alert-danger" v-if="error">{{ error }}</div>
        <input v-model="email" type="email" id="inputEmail" class="form-control" placeholder="Mail empresarial" required autofocus>
        <input v-model="password" type="password" id="inputPassword" class="form-control" placeholder="Contraseña" required>
        <div class="checkbox mb-3">
          <label>
            <input type="checkbox" value="remember-me"> Recordarme
          </label>
        </div>
        <vue-recaptcha @verify="markRecaptchaAsVerified" 
                       sitekey="6Ldz91wUAAAAAOB1DB-uII4Y3CgzAXBOxI7nwadF">
        </vue-recaptcha>
        <button @click="login" class="btn btn-lg btn-primary btn-block" type="submit" style="margin-top: 10px;">Ingresar</button>
      </form>
    </div>
  </div>
</template>

<script>
import User from 'src/utils/user.js'
import VueRecaptcha from 'vue-recaptcha'
export default {
  name: 'Login',
  components: { VueRecaptcha },
  data () {
    return {
      email: 'jorge@sura.com',
      password: 'pepe',
      error: false,
      recaptchaVerified: false
    }
  },
  created () {
    if (this.isLoggedIn()) {
      this.redirectToHome()
    }
  },
  updated () {
    if (this.isLoggedIn()) {
      this.redirectToHome()
    }
  },
  methods: {
    isLoggedIn () {
      return (localStorage.getItem('loggedIn') === 'true')
    },
    redirectToHome () {
      this.$router.push('/')
    },
    login () {
      let result = User.findByMail(this.email)
      if (result !== undefined && this.recaptchaVerified) {
        localStorage.setItem('loggedIn', 'true')
        User.setLoggedUser(result.email)
        this.redirectToHome()
      } else {
        if (result === undefined) {
          this.showLoginErrorNotification('top', 'center', 'Usuario inexistente')
        } else {
          this.showLoginErrorNotification('top', 'center', 'Debe completar el captcha')
        }
      }
    },
    showLoginErrorNotification (verticalAlign, horizontalAlign, msg) {
      const notification = {
        template: `<span>Error: <b>${msg}.<b></span>`
      }
      this.$notifications.notify(
        {
          component: notification,
          horizontalAlign: horizontalAlign,
          verticalAlign: verticalAlign,
          type: 'danger'
        })
    },
    markRecaptchaAsVerified (response) {
      this.recaptchaVerified = true
    }
  }
}
</script>

<style scoped>

i {
  font-size: 40px;
  color: gray;
  float: right;
  position: relative;
  top: 0px;
  margin-right: 10px;
}

.wrapper {
  background-image: url('/static/img/bg-7.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.multiplicate {
  /* background-color: #000;
  opacity: 0.7; */
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.form-signin {
  width: 330px;
  padding: 15px;
  margin: auto;
  opacity: 1 !important;
  background-color: white;
  border-radius: 3px;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

</style>



