<template>
  <div>    
    <div class="row">
      <div class="col-12">
        <card>
          <h4 slot="header" class="card-title">Solicitar</h4>
          <form>
            
            <div v-if="licence.type === 'Vacaciones'" class="row">
              <div class="col-md-12">
                <div class="alert alert-success" role="alert">
                  Tienes 6 días de vacaciones disponibles
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-md-4">
                <div class="form-group">
                  <label for="type">Tipo</label>
                  <select class="form-control" id="type" name="type"
                  v-model="licence.type" v-validate="'required'" 
                  :class="{ [`has-status-error`]: errors.first('type') }">
                    <option value="" disabled selected hidden>Seleccione un valor...</option>
                    <option v-for="ltype in types" :key="ltype">{{ltype}}</option>
                  </select>
                </div>
              </div>
              <div class="col-md-8">
                <div class="form-group">
                  <label for="type">Aprobador</label>
                  <select class="form-control" id="type" name="inHandsOf" 
                  v-model="licence.inHandsOf" v-validate="'required'" 
                  :class="{ [`has-status-error`]: errors.first('inHandsOf') }">
                    <option value="" disabled selected hidden>Seleccione un aprobador...</option>
                    <option v-for="a in approvers" :key="a.email">{{a.email}}</option>
                  </select>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label >Fecha de inicio</label>
                  <input type="date" name="iniDate" max="3000-12-31" min="1000-01-01" 
                  class="form-control" v-model="licence.iniDate" v-validate="'required'" 
                  :class="{ [`has-status-error`]: errors.first('iniDate') }">
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label >Fecha de fin</label>
                  <input type="date" name="endDate" max="3000-12-31" min="1000-01-01" 
                  class="form-control" v-model="licence.endDate" v-validate="'required'" 
                  :class="{ [`has-status-error`]: errors.first('endDate') }" >
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-md-12">
                <div class="alert alert-warning" role="alert">
                  (La fecha de fin debe ser el dia hábil en el que te reincorporas al trabajo)
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label for="notes">Observaciones</label>
                  <textarea class="form-control" id="noted" rows="4" 
                  placeholder="Escriba las observaciones que crea necesarias..." 
                  v-model="licence.notes"></textarea>
                </div>
              </div>
            </div>

            <div class="text-center">
              <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="updateProfile">
                Solicitar
              </button>
            </div>
            <div class="clearfix"></div>
          </form>
        </card>
      </div>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'
  import Alerts from 'src/utils/alerts.js'
  import Utils from 'src/utils/utils.js'

  export default {
    components: {
      Card
    },
    data () {
      return {
        licence: {
          inHandsOf: '',
          date: '',
          status: 'Pendiente',
          type: '',
          iniDate: '',
          endDate: '',
          notes: '',
          days: 10,
          actions: null
        },
        types: [
          'Cambio de Turno',
          'Casamiento',
          'Enfermedad',
          'Enfermedad por ART',
          'Examen',
          'Fallecimiento',
          'Falta injustificada',
          'Licencia sin goce de sueldo',
          'Maternidad',
          'Nacimiento',
          'Trabajo Fuera de la Oficina',
          'Vacaciones'
        ],
        approvers: []
      }
    },
    created () {
      this.approvers = User.getBossUsers().filter(u => u.email !== User.loggedUserId())
    },
    methods: {
      updateProfile () {
        this.$validator.validateAll().then(res => {
          if (res) {
            this.licence.date = Utils.formatDate(new Date())
            this.licence.applicant = User.loggedUserId()
            User.addLicence(this.licence)
            Alerts.showNotification(this, 'Licencia Solicitada', '', 'top', 'center', 'success')
            this.$router.push('/admin/licence/status')
          } else {
            Alerts.showNotification(this, 'Por favor complete los campos', '', 'top', 'center', 'danger')
          }
        })
      }
    }
  }

</script>
<style scoped> 
.has-status-error {
  border-color: red;
}
</style>



