<template>
  <ul class="nav nav-mobile-menu">
    <li class="nav-item">
      <a class="nav-link" href="#" @click="logout">
        <span class="no-icon">Cerrar Sesión</span>
      </a>
    </li>
  </ul>
</template>
<script>
  import DropDown from 'src/components/UIComponents/Dropdown.vue'
  import User from 'src/utils/user.js'
  export default {
    components: {
      DropDown
    },
    methods: {
      logout () {
        User.removeLoggedUser()
        localStorage.removeItem('loggedIn')
        this.$router.go('/')
      }
    }
  }
</script>
<style>
</style>



