<template>
  <nav class="navbar navbar-expand-lg">
    <div class="container-fluid">
      <a class="navbar-brand">
        <img class="rounded-circle" :src="`static/img/faces/${photo}.jpg`" width="27" height="27"> {{name}}</a>
      <button type="button"
              class="navbar-toggler navbar-toggler-right"
              :class="{toggled: $sidebar.showSidebar}"
              aria-controls="navigation-index"
              aria-expanded="false"
              aria-label="Toggle navigation"
              @click="toggleSidebar">
        <span class="navbar-toggler-bar burger-lines"></span>
        <span class="navbar-toggler-bar burger-lines"></span>
        <span class="navbar-toggler-bar burger-lines"></span>
      </button>
      <div class="collapse navbar-collapse justify-content-end">
        <ul class="nav navbar-nav mr-auto">
        </ul>
        <ul class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link :to="{path:'/admin/inbox'}" v-if="bossUser">
            <a class="nav-link" href="#">
              <i class="nc-icon nc-email-85"></i>
              <b class="caret"></b>
              <span class="notification" v-if="receivedLicencesCount !== 0">{{receivedLicencesCount}}</span>
            </a>
            </router-link>
          </li>
          <li class="nav-item">
            <router-link :to="{path:'/admin/user'}">
            <a class="nav-link" href="#">
              <i class="nc-icon nc-circle-09"></i>
              <b class="caret"></b>
            </a>
            </router-link>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link" @click="logout">
              Cerrar Sesión
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>
<script>
import User from 'src/utils/user.js'
export default {
  computed: {
    routeName () {
      const {name} = this.$route
      return this.capitalizeFirstLetter(name)
    },
    name () {
      let u = User.loggedUser()
      return u.firstName + ' ' + u.lastName
    },
    photo () {
      return User.loggedUser().photo
    },
    bossUser () {
      return User.getUserRole() === 'boss'
    },
    currentUser () {
      return User.loggedUserId()
    },
    receivedLicencesCount () {
      return User.getAllLicences().filter(l => l.status !== 'Aprobado' && l.status !== 'Cancelado' && l.inHandsOf === this.currentUser).length
    }
  },
  data () {
    return {
      activeNotifications: false
    }
  },
  methods: {
    capitalizeFirstLetter (string) {
      return string.charAt(0).toUpperCase() + string.slice(1)
    },
    toggleNotificationDropDown () {
      this.activeNotifications = !this.activeNotifications
    },
    closeDropDown () {
      this.activeNotifications = false
    },
    toggleSidebar () {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
    },
    hideSidebar () {
      this.$sidebar.displaySidebar(false)
    },
    logout () {
      User.removeLoggedUser()
      localStorage.removeItem('loggedIn')
      this.$router.go('/')
    },
    goToInbox () {
      this.$router.go('/admin/paycheck')
    }
  }
}
</script>
<style>

</style>



