<template>
  <div class="row">
    <div class="col-12">
      <card>
        <template slot="header">
          <h4 class="card-title">Mis licencias</h4>
          <p class="card-category"></p>
        </template>
        <div v-if="licences.length === 0" class="alert alert-warning" role="alert">No has solicitado ninguna licencia.</div>
        <div v-else class="table-responsive">
          <table class="table table-hover table-striped">
            <thead>
              <tr>
                <th>Fecha</th>
                <th>En manos de</th>
                <th>Tipo</th>
                <th>Fecha de Inicio</th>
                <th>Fecha de regreso</th>
                <th>Días</th>
                <th>Estado</th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(l, index) in licences" :key="index">
                <td>{{l.date}}</td>
                <td>{{l.inHandsOf}}</td>
                <td><b>{{l.type}}</b></td>
                <td>{{l.iniDate}}</td>
                <td>{{l.endDate}}</td>
                <td>{{l.days}}</td>
                <td :class="{ 'text-success' : l.status === 'Aprobado', 'text-danger' : l.status === 'Cancelado', 'text-warning' : l.status === 'Pendiente'}"><b>{{l.status}}</b></td>
                <td>
                  <div class="input-group-prepend" v-if="l.status !== 'Cancelado'">
                    <button class="btn btn-fill btn-info dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >Acciones </button>
                    <div class="dropdown-menu">
                      <a class="dropdown-item" @click="cancelLicence(index)">Cancelar</a>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'
  import Alerts from 'src/utils/alerts.js'

  export default {
    components: {
      Card
    },
    data () {
      return {
        licences: []
      }
    },
    created () {
      this.licences = User.loggedUser().licences
    },
    methods: {
      updateLocalStorage (userMail, licences) {
        User.updateUserLicences(userMail, licences)
      },
      cancelLicence (id) {
        this.licences[id].status = 'Cancelado'
        this.licences[id].inHandsOf = ''
        this.updateLocalStorage(User.loggedUser().email, this.licences)
        Alerts.showNotification(this, 'Solicitud Cancelada', '', 'top', 'center', 'danger')
      }
    }
  }
</script>
<style>

</style>



