<template>
  <div class="row">
    <div class="col-12">
      <card>
        <template slot="header">
          <h4 class="card-title">Solicitudes recibidas</h4>
          <p class="card-category"></p>
        </template>
        <div v-if="this.receivedLicences === 0" class="alert alert-warning" role="alert">No has recibido ninguna solicitud.</div>
        <div v-else class="table-responsive">
          <table class="table table-hover table-striped">
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Solicitante</th>
                <th>En manos de</th>
                <th>Tipo</th>
                <th>Fecha de Inicio</th>
                <th>Fecha de Regreso</th>
                <th>Días</th>
                <th>Estado</th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(l, index) in licences" :key="index" v-if="l.status !== 'Aprobado' && l.status !== 'Cancelado' && l.inHandsOf === currentUser">
                <td>{{l.date}}</td>
                <td>{{l.applicant}}</td>
                <td>{{l.inHandsOf}}</td>
                <td><b>{{l.type}}</b></td>
                <td>{{l.iniDate}}</td>
                <td>{{l.endDate}}</td>
                <td>{{l.days}}</td>
                <td class="text-warning"><b>{{l.status}}</b></td>
                <td>
                  <div class="input-group-prepend">
                    <button class="btn btn-fill btn-info dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Acciones </button>
                    <div class="dropdown-menu">
                      <a class="dropdown-item" href="#" @click="acceptLicence(index)">Aceptar</a>
                      <a class="dropdown-item" href="#" @click="cancelLicence(index)">Cancelar</a>
                    </div>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'
  import Alerts from 'src/utils/alerts.js'

  export default {
    components: {
      Card
    },
    data () {
      return {
        licences: []
      }
    },
    created () {
      this.licences = User.getAllLicences()
    },
    computed: {
      currentUser () {
        return User.loggedUserId()
      },
      receivedLicences () {
        return this.licences.filter(l => l.status !== 'Aprobado' && l.status !== 'Cancelado' && l.inHandsOf === this.currentUser).length
      }
    },
    methods: {
      updateLocalStorage (userMail, licences) {
        User.updateUserLicences(userMail, licences)
      },
      acceptLicence (id) {
        this.licences[id].status = 'Aprobado'
        this.licences[id].inHandsOf = ''
        this.updateLocalStorage(this.licences[id].applicant, this.licences)
        Alerts.showNotification(this, 'Solicitud Aprobada', '', 'top', 'center', 'success')
      },
      cancelLicence (id) {
        this.licences[id].status = 'Cancelado'
        this.licences[id].inHandsOf = ''
        this.updateLocalStorage(this.licences[id].applicant, this.licences)
        Alerts.showNotification(this, 'Solicitud Cancelada', '', 'top', 'center', 'danger')
      }
    }
  }
</script>
<style>

</style>



