<template>
  <card class="card-user">
    <img slot="image" src="https://ununsplash.imgix.net/photo-1431578500526-4d9613015464?fit=crop&fm=jpg&h=300&q=75&w=400" alt="..."/>
    <div class="author">
      <a href="#">
        <img class="avatar border-gray" :src="`static/img/faces/${user.photo}.jpg`" alt="..."/>
        <h4 class="title">{{user.firstName}} {{user.lastName}}<br />
          <small></small>
        </h4>
      </a>
    </div>
    <p class="description text-center">{{user.position}}
    </p>
    <div slot="footer" class="text-center">
    </div>
  </card>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'

  export default {
    components: {
      Card
    },
    computed: {
      user () {
        return User.loggedUser()
      }
    },
    methods: {
      getClasses (index) {
        var remainder = index % 3
        if (remainder === 0) {
          return 'col-md-3 col-md-offset-1'
        } else if (remainder === 2) {
          return 'col-md-4'
        } else {
          return 'col-md-3'
        }
      }
    }
  }

</script>
<style>

</style>



