function showNotification (app, title, text, verticalAlign, horizontalAlign, level) {
  const notification = {
    template: `<div><span><b>${title}</b></span><span><b>${text}</b></span></div>`
  }
  app.$notifications.notify(
    {
      component: notification,
      horizontalAlign: horizontalAlign,
      verticalAlign: verticalAlign,
      type: level
    })
}

export default {
  showNotification
}



