function loggedUserId () {
  return localStorage.getItem('user')
}

function setLoggedUser (userMail) {
  localStorage.setItem('user', userMail)
}

function getUserRole () {
  let user = loggedUser()
  return user.role
}

function removeLoggedUser () {
  localStorage.removeItem('user')
}

function hasInitialData () {
  return localStorage.getItem('users') !== null
}

function find (userId, users) {
  return users.find(u => u.email === userId)
}

function findByMail (mail) {
  return find(mail, JSON.parse(localStorage.getItem('users')))
}

function loggedUser () {
  return find(loggedUserId(), JSON.parse(localStorage.getItem('users')))
}

function getUsers () {
  return JSON.parse(localStorage.getItem('users'))
}

function getUsersId () {
  return getUsers().map(u => u.email)
}

function getBossUsers () {
  return getUsers().filter(u => u.role === 'boss')
}

function getAllNewLicenseRequests () {
  return Array.prototype.concat(...JSON.parse(localStorage.getItem('users'))
                        .map(e => e.licences))
                        .filter(l => l.status !== 'Aprobado')
}

function getAllLicences () {
  return Array.prototype.concat(...JSON.parse(localStorage.getItem('users'))
                        .map(e => e.licences))
}

function getAllLicenseHistory () {
  // TODO: Ver filtro para licencias que estan en recibidos...
  return Array.prototype.concat(...JSON.parse(localStorage.getItem('users'))
                                        .map(e => e.licences))
                                        .filter(l => l.status !== 'Pendiente')
}

function update (user) {
  let newUsers = []
  getUsers().forEach(u => {
    if (u.email === user.email) {
      newUsers.push(user)
    } else {
      newUsers.push(u)
    }
  })
  localStorage.setItem('users', JSON.stringify(newUsers))
}

function addLicence (lic) {
  let user = loggedUser()
  user.licences.push(lic)
  update(user)
}

function updateUserLicences (userMail, licences) {
  let user = findByMail(userMail)
  user.licences = licences
  update(user)
}

