import DashboardLayout from '../components/Dashboard/Layout/DashboardLayout.vue'

// Admin pages
import UserProfile from 'src/components/Dashboard/Views/UserProfile.vue'
import Licence from 'src/components/Dashboard/Views/Licence.vue'
import Paycheck from 'src/components/Dashboard/Views/Paycheck.vue'
import Status from 'src/components/Dashboard/Views/Licence/Status.vue'
import Apply from 'src/components/Dashboard/Views/Licence/Apply.vue'
import Login from 'src/components/GeneralViews/Login.vue'
import Inbox from 'src/components/Dashboard/Views/Inbox.vue'
import ReceivedInbox from 'src/components/Dashboard/Views/Inbox/Received.vue'
import HistoryInbox from 'src/components/Dashboard/Views/Inbox/History.vue'

const routes = [
  {
    path: '/',
    redirect: '/admin'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/admin',
    component: DashboardLayout,
    redirect: '/admin/user',
    children: [
      {
        path: 'inbox',
        name: 'Inbox',
        component: Inbox,
        redirect: '/admin/inbox/received',
        children: [
          {
            path: 'received',
            name: 'ReceivedInbox',
            component: ReceivedInbox
          },
          {
            path: 'history',
            name: 'HistoryInbox',
            component: HistoryInbox
          }
        ]
      },
      {
        path: 'user',
        name: 'User',
        component: UserProfile
      },
      {
        path: 'paycheck',
        name: 'Paycheck',
        component: Paycheck
      },
      {
        path: 'licence',
        name: 'Licence',
        component: Licence,
        redirect: '/admin/licence/status',
        children: [
          {
            path: 'status',
            name: 'Status',
            component: Status
          },
          {
            path: 'apply',
            name: 'Apply',
            component: Apply
          }
        ]
      }
    ]
  },
  { path: '*',
    redirect: '/'
  }
]

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes



