<template>
  <div class="content">
      <div class="container-fluid">
        <div class="row">
          <div class="col-12">
            <ul class="nav nav-tabs">
              <li><router-link class="nav-link" to="/admin/inbox/received" link="/admin/inbox/received">Recibidos <span class="badge badge-secondary" v-if="receivedLicencesCount !== 0">{{receivedLicencesCount}}</span></router-link></li>
              <li><router-link class="nav-link" to="/admin/inbox/history" link="/admin/inbox/history">Historial</router-link></li>          
            </ul>
          </div>
        </div>
        <router-view></router-view>
      </div>
  </div>
</template>
<script>
  import User from 'src/utils/user.js'
  export default {
    data () {
      return {
      }
    },
    computed: {
      bossUser () {
        return User.getUserRole() === 'boss'
      },
      receivedLicencesCount () {
        return User.getAllLicences().filter(l => l.status !== 'Aprobado' && l.status !== 'Cancelado' && l.inHandsOf === this.currentUser).length
      }
    }
  }
</script>
<style>
</style>



