<template>
  <div class="row">
    <div class="col-12">
      <card>
        <template slot="header">
          <h4 class="card-title">Historial de solicitudes</h4>
          <p class="card-category"></p>
        </template>
        <div v-if="this.licencesHistory === 0" class="alert alert-warning" role="alert">No hay solicitudes a mostrar.</div>
        <div v-else class="table-responsive">
          <table class="table table-hover table-striped">
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Solicitante</th>
                <th>Tipo</th>
                <th>Fecha de Inicio</th>
                <th>Fecha de regreso</th>
                <th>Días</th>
                <th>Estado</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(l, index) in licences" :key="index" v-if="l.status !== 'Pendiente'">
                <td>{{l.date}}</td>
                <td>{{l.applicant}}</td>
                <td><b>{{l.type}}</b></td>
                <td>{{l.iniDate}}</td>
                <td>{{l.endDate}}</td>
                <td>{{l.days}}</td>
                <td :class="{ 'text-success' : l.status === 'Aprobado', 'text-danger' : l.status === 'Cancelado' }"><b>{{l.status}}</b></td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'

  export default {
    components: {
      Card
    },
    data () {
      return {
        licences: []
      }
    },
    created () {
      this.licences = User.getAllLicences()
    },
    computed: {
      currentUser () {
        return User.loggedUserId()
      },
      licencesHistory () {
        return this.licences.filter(l => l.status !== 'Pendiente').length
      }
    },
    methods: {
    }
  }
</script>
<style>

</style>



