<template>
  <card>
    <h4 slot="header" class="card-title">Datos Personales</h4>
    <form>
      <div class="row">
        <div class="col-md-5">
          <fg-input type="text"
                    label="Empresa"
                    :disabled="true"
                    placeholder="Empresa"
                    v-model="user.company">
          </fg-input>
        </div>
        <div class="col-md-7">
          <fg-input type="email"
                    label="E-mail Laboral"
                    :disabled="true"
                    placeholder="E-mail Laboral"
                    v-model="user.email">
          </fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6">
          <fg-input type="text"
                    label="Nombres"
                    placeholder="Nombres"
                    v-model="user.firstName">
          </fg-input>
        </div>
        <div class="col-md-6">
          <fg-input type="text"
                    label="Apellidos"
                    placeholder="Apellidos"
                    v-model="user.lastName">
          </fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="E-mail Personal"
                    placeholder="E-mail Personal"
                    v-model="user.personalEmail">
          </fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="Dirección"
                    placeholder="Dirección"
                    v-model="user.address">
          </fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-4">
          <fg-input type="text"
                    label="Ciudad"
                    placeholder="Ciudad"
                    v-model="user.city">
          </fg-input>
        </div>
        <div class="col-md-4">
          <fg-input type="text"
                    label="País"
                    placeholder="País"
                    v-model="user.country">
          </fg-input>
        </div>
        <div class="col-md-4">
          <fg-input type="number"
                    label="Código Postal"
                    placeholder="Código Postal"
                    v-model="user.postalCode">
          </fg-input>
        </div>
      </div>

      <div class="text-center">
        <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="updateProfile">
          Actualizar
        </button>
      </div>
      <div class="clearfix"></div>
    </form>
  </card>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import User from 'src/utils/user.js'
  import Alerts from 'src/utils/alerts.js'

  export default {
    components: {
      Card
    },
    data () {
      return {
        user: User.loggedUser()
      }
    },
    methods: {
      updateProfile () {
        User.update(this.user)
        Alerts.showNotification(this, 'Usuario Actualizado', '', 'top', 'center', 'success')
      }
    }
  }

</script>
<style>

</style>



